  width: 1200px;
  grid-template-areas: 
  "header header"
  "nav conten"
  ;

  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /* grid-gap: 10px; */
}

.app-wrapper-content{
grid-area: conten;
background-color: burlywood;
}






